export default {
	data() {
		return {
			class_id: '',
			faqsClass: '',
			activeNum: 0,
			dataFormList: []
		};
	},
	onLoad:function(){
		this._getFaqsClass()
	},
	methods:{
		// 获取问答分类
		_getFaqsClass () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('Visitor/get_faqs_class').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.faqsClass = res.data.data
					this._getFaqs(res.data.data[0].id)
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 获取问答列表
		_getFaqs (id) {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post(
			'Visitor/get_faqs',
				'\r\n--XXX' +
				'\r\nContent-Disposition: form-data; name="class_id"' +
				'\r\n' +
				'\r\n' + id +
				'\r\n--XXX--'
				).then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.dataFormList = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		activeClick(num) {
			if (num == this.activeNum) {
				return false
			}
			this.activeNum = num
			this._getFaqs(this.faqsClass[num].id)
		}
	}
}
